@import "../partials/variables";

//Theme 11
#atw_style11{
    .atw__title11{
        padding-bottom: 0;
        margin-bottom: 45px;
        &:before{
            content: none;
        }
        h3{
            text-align: left;
        }
    }
    .atw--single_item{
        border-radius: 5px;
        padding: 10px;
        .atw_item_top{
            border-radius: 5px;
            overflow: hidden;
            position: relative;
            .badge--left{
                left: 0;
                top: 0;
            }
            .badge--right{
                right: 0;
                top: 0;
            }
            .atw_item_view{
                position: absolute;
                width: 100%;
                left: 0;
                top: 0;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: $transition;
                visibility: hidden;
                opacity: 0;
                a{
                    padding: 0 25px;
                    line-height: 40px;
                    background: #fff;
                    color: #000;
                    font-size: 12px;
                    font-weight: 500;
                    text-transform: uppercase;
                    border-radius: 25px;
                    display: inline-block;
                    transition: $transition;
                    margin-top: 20px;
                }
            }
        }
        .atw_item_bottom{
            .atw_item_info{
                padding: 20px 0;
                text-align: center;
                .atw_item_title{
                    font-weight: 400;
                    font-size: 16px;
                    margin-bottom: 12px;
                    a{
                        color:#363940;
                    }
                }
                .atw_price, .atw_rating{
                    display: block;
                }
                .atw_price{
                    color: $theme2;
                    margin-bottom: 10px;
                }
            }
        }
        &:hover{
            .atw_item_top{
                .atw_item_view{
                    visibility: visible;
                    opacity: 1;
                    background: rgba(0,0,0,0.2);
                    a{
                        margin-top: 0;
                    }
                }
            }
        }
    }
    .atw_slider_controls{
        .slider--control11{
            position: absolute;
            right: 0;
            margin: 0 4px 0 4px;
            top: 0;
            transform: none;
            border-radius: 3px;
            transition: 0.3s ease;
            border: 1px solid $borderColor;
            &:hover{
                border-color: $theme2;
                color: #fff;
                background: $theme2;
            }
            &.icon-arrow-left{
                left: auto;
                right: 60px;
            }
            &.icon-arrow-right{
                right: 11px;
            }
        }
    }
}